import React, {useState, useEffect} from 'react';
import Axios from 'axios';
import ReactHighcharts from 'react-highcharts/ReactHighstock.src';
import './Chart.css'


function Chart(props) {
  const API_KEY = ['BWIY4NDHJIXLK06R','Q4OZ3T6KRYIMRRZU','32121ZHS4J6CRSMB','BSUL8MD6S4AAI3RC','A5K8VKW29UWFEZCE','PNFFMHFJXQKIZL3H',
                'Q0JN9NEW3S5T9BGM','KWFH468DBZ92ZWMG','Z8MQYC2X9QDL09TG','0F8QE3FDV05NTOKM']
    let [price, setPrice] = useState([])
    let [price2, setPrice2] = useState([])
    let title=props.name
    if(props.symbol2){
      title = props.symbol + '  v  ' + props.symbol2
    }
    if (Object.keys(props.price).length){
      title = title + '    $' + props.price['price']
    }
    useEffect( () => {
        let url = `https://www.alphavantage.co/query?function=TIME_SERIES_DAILY&symbol=${props.symbol}&apikey=${API_KEY[3]}`
        Axios.get(url).then((res) => {
            if(res.data['Note']==="Thank you for using Alpha Vantage! Our standard API call frequency is 5 calls per minute and 500 calls per day. Please visit https://www.alphavantage.co/premium/ if you would like to target a higher API call frequency."){
              throw new Error('Api calls exhausted')
            }
            let data = res.data['Time Series (Daily)']
            let priceData=[]
            for ( let value in data){
                var myDate=value.split('-');
                myDate = new Date(myDate[0],myDate[1]-1,myDate[2])
                priceData.push([myDate.getTime(),parseFloat(data[value]['4. close'])])
            }
            priceData.sort((a,b)=>a[0]-b[0])
            setPrice(priceData)
            
        })

        if(props.symbol2){
            let url = `https://www.alphavantage.co/query?function=TIME_SERIES_DAILY&symbol=${props.symbol2}&apikey=${API_KEY[3]}`
            Axios.get(url).then((res) => {
                if(res.data['Note']==="Thank you for using Alpha Vantage! Our standard API call frequency is 5 calls per minute and 500 calls per day. Please visit https://www.alphavantage.co/premium/ if you would like to target a higher API call frequency."){
                  throw new Error('Api calls exhausted')
                }
                let data = res.data['Time Series (Daily)']
                let priceData=[]
                for ( let value in data){
                    var myDate=value.split('-');
                    myDate = new Date(myDate[0],myDate[1]-1,myDate[2])
                    priceData.push([myDate.getTime(),parseFloat(data[value]['4. close'])])
                }
                priceData.sort((a,b)=>a[0]-b[0])
                setPrice2(priceData)
                
            })
        }
    },[props.symbol])

    var config = {
        chart: {
          type: 'spline',
          height: props.height,
          width: props.width
        },
        xAxis:{
          type: 'date'
        },
        yAxis: [{
          offset: 20,
          labels: {
            x: -15,
            align: 'left'
          },
        }, 
        ],
        title: {
          text: title
        },
        tooltip:{
          animation: true,
          backgroundColor: '#e7e7de',
          borderRadius: 8
        },
        rangeSelector: {
          buttons: [{
            type: 'day',
            count: 1,
            text: '1d',
          }, {
            type: 'day',
            count: 7,
            text: '7d'
          }, {
            type: 'month',
            count: 1,
            text: '1m'
          }, {
            type: 'month',
            count: 3,
            text: '3m'
          },
            {
              type:'years',
              count:1,
              text:'1y'
          },
            {
            type: 'all',
            text: 'All'
          }],
          selected: 5
        },
        series: [
          { 
            name: props.symbol,
            data: price,
            allowPointSelect: true,
            color: 'green',
            showInNavigator: true
          },
          {
            name: props.symbol2,
            data: price2,
            allowPointSelect: true,
            color: 'black',
            showInNavigator: true
          }
        ]
      };

    return (
        <>
            <div  className='chart'>
                <ReactHighcharts  config={config} />
            </div>
        </>
    )
}

export default Chart
