import React, {useState, useRef} from 'react'
import './Compare.css'
import Chart from '../Chart'


function Compare() {

    const [symbol1,setSymbol1] = useState('');
    const [symbol2,setSymbol2] = useState('');
    const company1 = useRef()
    const company2 = useRef()

    const onSubmit = (event) => {
        event.preventDefault();
        let c1 = company1.current.value;
        company1.current.value='';
        let c2 = company2.current.value;
        company2.current.value='';
        setSymbol1(c1);
        setSymbol2(c2);

    }

    return (
        <>
        <div className='compare'>
            <form onSubmit={onSubmit}>
                <input  name='company1' type='text' placeholder='Enter first comapny' size='50' ref={company1} required></input>
                <input  name='company2' type='text' placeholder='Enter second comapny' size='50' ref={company2} required></input>
                <input type='submit' value='compare' />
            </form>
        </div>
        <div className='compare-chart'>
            {symbol1.length!==0 ? 
                <Chart name='' price='' symbol={symbol1} symbol2={symbol2} height='400' width='1440'/>
                
            : ''}
        </div>
        </>

    )
}

export default Compare
