import React, {useState, useRef} from 'react';
import Axios from 'axios';
import './Company.css'

function Company(props) {

    const symbolInput = useRef();

    const API_KEY = ['BWIY4NDHJIXLK06R','Q4OZ3T6KRYIMRRZU','32121ZHS4J6CRSMB','BSUL8MD6S4AAI3RC','A5K8VKW29UWFEZCE','PNFFMHFJXQKIZL3H',
                'Q0JN9NEW3S5T9BGM','KWFH468DBZ92ZWMG','Z8MQYC2X9QDL09TG','0F8QE3FDV05NTOKM']
    const [suggestion,setSuggestion] = useState([])
    const [bookmarks,setBookmarks] = useState(['GME','AAPL','AMZN','TSLA','FB','GOOGL','MSFT'])

    //  For suggestion endpoint
    //     const onChange = (event) => {
    //     let index = Math.floor(Math.random()*10)
    //     let search = event.target.value
    //     let url = `https://www.alphavantage.co/query?function=SYMBOL_SEARCH&keywords=${search}&apikey=${API_KEY[index]}`
    //     Axios.get(url).then((res)=> {
    //         let data = res.data["bestMatches"];
    //         setSuggestion(data)
    //     })
    // }

    const onSubmit = (event) => {
        event.preventDefault();
        let symbol = symbolInput.current.value
        props.setSymbol(symbol)
        symbolInput.current.value=''
        let book=[...bookmarks]
        let flag=true;
        if (book.length>=19){
            flag=false
        }
        else{
            for(let i=0;i<book.length;i++){
                if(book[i]===symbol){
                    flag=false;
                }
            }
        }
        if(flag){
            setBookmarks([...bookmarks,symbol])
        }
    }
    
    const removeBook = (index) => {
        let book = [...bookmarks]
        let temp = book.filter((v,i)=> i!==index)
        setBookmarks(temp)
    }


    return (
        <div>
            <form onSubmit={onSubmit}>
                <input classname='search' name='search' type='text' placeholder='Enter comapny name' size='50'  ref={symbolInput}></input>
            </form>
            
            
            <ul className='company-ul'>
                {bookmarks.length>0 ? bookmarks.map((value,index)=> <div className='company-div'><ul className='inner-company-ul'><li className='company-li' key={value} onClick={()=>props.setSymbol(value)} type='None'>{value} </li><li type='None'><button onClick={()=>removeBook(index)}>X</button></li></ul></div>) : null}
            </ul>
            

            {/* <ul>
                {suggestion.length>0 ? suggestion.map((value,index)=> <li key={value['1. symbol']} onClick={()=>props.setSymbol(value['1. symbol'])} type='None'>{value['1. symbol']} ( {value['2. name']} )</li>) : null}
            </ul> */}
        </div>
    )
}

export default Company
