import React, {useState, useEffect} from 'react';
import Axios from 'axios';
import './Share.css'

function Share(props) {
    const API_KEY = ['BWIY4NDHJIXLK06R','Q4OZ3T6KRYIMRRZU','32121ZHS4J6CRSMB','BSUL8MD6S4AAI3RC','A5K8VKW29UWFEZCE','PNFFMHFJXQKIZL3H',
                        'Q0JN9NEW3S5T9BGM','KWFH468DBZ92ZWMG','Z8MQYC2X9QDL09TG','0F8QE3FDV05NTOKM']
    
    const [quote,setQuote] = useState({})
    const [overview,setOverview] = useState({})
    const [description, setDescription] = useState()

    useEffect( () => {
        let index = Math.floor(Math.random()*10)
        let URL = `https://www.alphavantage.co/query?function=GLOBAL_QUOTE&symbol=${props.symbol}&apikey=${API_KEY[1]}`
        index = Math.floor(Math.random()*10)
        let URL2 =`https://www.alphavantage.co/query?function=OVERVIEW&symbol=${props.symbol}&apikey=${API_KEY[2]}`
        Axios.get(URL).then((res) => {
            if(res.data['Note']==="Thank you for using Alpha Vantage! Our standard API call frequency is 5 calls per minute and 500 calls per day. Please visit https://www.alphavantage.co/premium/ if you would like to target a higher API call frequency."){
              throw new Error('Api calls exhausted')
            }
            let data = res.data["Global Quote"]
            let price = {
                "price": data["05. price"],
                "change": data["09. change"]
            }
            props.setPrice(price)
            let temp = {
                "Open": data["02. open"],
                "High": data["03. high"],
                "Low": data["04. low"],
                "Volume": data["06. volume"]
            }
            setQuote(temp)
            
        })
        Axios.get(URL2).then((res) => {
            if(res.data['Note']==="Thank you for using Alpha Vantage! Our standard API call frequency is 5 calls per minute and 500 calls per day. Please visit https://www.alphavantage.co/premium/ if you would like to target a higher API call frequency."){
              throw new Error('Api calls exhausted')
            }
            let data = res.data
            let str=''
            let description = data["Description"]
            props.setName(data["Name"])
            if(description!==undefined){
                for (let i = 0;i<description.length;i++){
                    str+=description[i];
                    if(description[i]==='.'){
                        if(str.length>500){
                            break
                        }
                    }
                }
            }
            setDescription(str)
            let temp = {
                "52W H": data["52WeekHigh"],
                "52W L": data["52WeekLow"],
                "Mkt Cap": data["MarketCapitalization"],
                "P/E": data["PERatio"],

                
                "Beta": data["Beta"],
                "EPS": data["EPS"],

                "50D Avg": data["50DayMovingAverage"],
                "200D Avg": data["200DayMovingAverage"],
                // "Last Split Factor": data["LastSplitFactor"],
                // "Last Split Date": data["LastSplitDate"]
            }
            setOverview(temp)
            
        })

    },[props.symbol])

    const test = () => {
        let arr = new Array()
        for(let keys in quote){
            if(quote[keys]==='None'){
                continue;
            }
            arr.push(<ul key={`ul-${keys}`} className='inner-ul'><li key={keys} type='None' className='bold-list'>{keys}</li><li key={`value-${keys}`} type='None'>{quote[keys]}</li></ul>)
        }
        for(let keys in overview){
            if(overview[keys]==='None'){
                continue;
            }
            arr.push(<ul key={`ul-${keys}`} className='inner-ul'><li key={keys} type='None' className='bold-list'>{keys}</li><li key={`value-${keys}`} type='None'>{overview[keys]}</li></ul>)
        }
        return arr
    }

    return (
        <div className='share-info'>
            <div className='description'>
                {description}
            </div>
            <ul className='outer-ul'>
                {test()}
            </ul>
        </div>
    )
}

export default Share
