import React, {useState} from 'react';
import {BrowserRouter as Router, Route, Redirect, Switch} from 'react-router-dom';
import './App.css';
import Chart from './components/Chart'
import Share from './components/Share'
import Header from './components/Header'
import Company from './components/Company'
import Compare from './components/pages/Compare'
import ErrorBoundary from './components/errors/ErrorBoundary'

function App() {

  const [name,setName] = useState()
  const [symbol,setSymbol] = useState('GME')
  const [price,setPrice] = useState({})


  return (
    <Router>
      <div className="App">
        
        <Switch>
          <Route path='/stock' exact>
              <div className='header'>
                <Header />
              </div>
              <div className='content'>
                <div className='company-list'>
                  <div>
                    <Company setSymbol={setSymbol}/>
                  </div>
                </div>
                <div className='content-chart'>
                  <div className='main-chart'>
                    <ErrorBoundary><Chart name={name} symbol={symbol} price={price} height='400' width='1000'/></ErrorBoundary>
                  </div>
                  <div className='main-share'>
                    <Share className='in-share' setPrice={setPrice} setName={setName} symbol={symbol}/>
                  </div>
                </div>
              </div>
          </Route>
          <Route path='/stock/compare' exact>
              <div className='header'>
              <Header />
             </div>
            <Compare />
          </Route>
          <Redirect to='/stock' />
        </Switch>
      </div>
    </Router>
  );
}

export default App;
